<template>
  <el-container class="index">
    <el-aside width="180px">
      <el-menu router>
        <el-menu-item index="/index/discover">
          <img src="../assets/home.png" alt="" />
          <span>发现</span>
        </el-menu-item>
        <el-menu-item index="/index/artists">
          <img src="../assets/artist.png" alt="" />
          <span>歌手</span>
        </el-menu-item>
        <el-menu-item index="/index/playlists">
          <img src="../assets/music.png" alt="" />
          <span>歌单</span>
        </el-menu-item>
        <el-menu-item index="/index/toplists">
          <img src="../assets/list.png" alt="" />
          <span>排行榜</span>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-container>
      <el-header>
        <el-button @click="goback" type="text">
          <img src="../assets/left.png" />
        </el-button>
        <el-button @click="goahead" type="text">
          <img src="../assets/right.png" />
        </el-button>
        <img src="../assets/logo.png" alt="" class="logo" />
        <el-input
          placeholder="搜索音乐/歌手/歌单"
          prefix-icon="el-icon-search"
          v-model="value"
          clearable
          @change="search"
        ></el-input>
      </el-header>
      <el-main>
        <router-view @getData="getData"></router-view>
      </el-main>
      <el-footer v-show="id">
        <div class="info">
          <img :src="src" alt="" />
          <div class="text">
            <span>{{ artist }} -- {{ song }}</span>
            <audio :src="url" controls autoplay loop ref="audio"></audio>
          </div>
        </div>
        <div class="control">
          <el-button type="text">
            <img src="../assets/up.png" alt="" />
          </el-button>
          <el-button type="text" @click="pause">
            <img v-if="display" src="../assets/play.png" alt="" />
            <img v-else src="../assets/pause.png" alt="" />
          </el-button>
          <el-button type="text">
            <img src="../assets/down.png" alt="" />
          </el-button>
        </div>
      </el-footer>
    </el-container>
  </el-container>
</template>
<script>
export default {
  data() {
    return {
      id: "",
      url: "",
      src: "",
      song: "",
      artist: "",
      keyword: "",
      value: "",
      display: false,
    };
  },
  methods: {
    getData(data) {
      // console.log(data);
      this.id = data.id;
      this.song = data.name;
      this.src = data.al.picUrl;
      this.artist = data.ar[0].name;
      this.url = `https://music.163.com/song/media/outer/url?id=${this.id}.mp3`;
      this.display = false;
    },
    search() {
      this.keyword = this.value;
      this.value = "";
      this.$router.push({ path: `/index/search/${this.keyword}` });
    },
    goback() {
      this.$router.go(-1);
    },
    goahead() {
      this.$router.go(1);
    },
    pause() {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
      } else {
        this.$refs.audio.pause();
      }
      this.display = this.$refs.audio.paused;
    },
  },
};
</script>

